@import "config";

@font-face {
  font-family: $font-family-default;
  src: url("../fonts/subset-OwnersText-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: $font-family-default;
  src: url("../fonts/subset-OwnersText-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: $font-family-accent;
  src: url("../fonts/subset-OwnersXWide-Bold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: $font-family-deco;
  src: url("../fonts/subset-LustScript.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: $font-family-default;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family-accent;
  margin-top: 0;
  padding-bottom: 20px;
}
